import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

const Header = () => (
    <div className="header"><img src={require("./recipe.png")} alt="recipe"/>
    </div>
)
const DirectoryView = (props) => (
    <div className="recipes">
        <SearchForm searchVal = {props.searchVal} handleChange={props.handleChange} selectRecipes={props.selectRecipes} />
        {props.selectedRecipes.map((recipe, i) => <RecipeCard name = {recipe.name} id={recipe.id} key={recipe.id} viewDetail={props.viewDetail} />)}
    </div>
)

const SearchForm = (props) => (
    <div>
        <input value={props.searchVal} onChange={props.handleChange} autoFocus />
        <button onClick={props.selectRecipes}>Search</button>
    </div>
)

const RecipeCard  = (props) => (
    <div>
        <p className="recipeList" onClick={()=> props.viewDetail(props.id)}>{props.name}</p>
    </div>
)

const DetailView = (props) => (

    <div className="detailRecipe">
        <p className="recipeName"><img src={require("./dish4.png")}  alt="dish"/>{props.currentRecipe.name}</p>       
        <p className="ingredients">
           <p className="title">Ingredients</p>
           {props.currentRecipe.ingredients.map(e => <p>{e}</p>)}
        </p>
        <p className="instructions">
           <p className="title">Instructions</p>
           {props.currentRecipe.instructions.map(e =><p>{e}</p>)}
        </p>
    </div>
)


